"use client"

import { FC, ReactNode, useEffect, useState } from "react"
import {ClerkProvider,useAuth,SignInButton} from "@clerk/clerk-react"
import {ConvexProviderWithClerk} from "convex/react-clerk"
import {Authenticated,Unauthenticated,ConvexReactClient} from "convex/react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card"
import { Label } from "@radix-ui/react-label"
import { Input } from "@/components/ui/input"

const CONVEX_URL=process.env.NEXT_PUBLIC_CONVEX_URL!;
const CLERK_PUBLISHABLE_KEY=process.env.NEXT_PUBLIC_CLERK_PUBLISHABLE_KEY!
const convex=new ConvexReactClient(CONVEX_URL)

const ConvexClientProvider:FC<{children:ReactNode}>=({children})=>{
    const [isMounted, setIsMounted] = useState(false);

  // Prevent hydration mismatch error
  useEffect(() => {
    setIsMounted(true);
  }, []);

  if (!isMounted) return null; // Avoid rendering on server-side
    return (
    <ClerkProvider publishableKey={CLERK_PUBLISHABLE_KEY}>
        <ConvexProviderWithClerk 
        client={convex}
        useAuth={useAuth}
        >
            <Authenticated>
               {children} 
            </Authenticated>
            <Unauthenticated>
            <ConvexProviderWithClerk
            
        client={convex}
        useAuth={useAuth}>
            <Card className="w-[350px]">
      <CardHeader>
        <CardTitle>Nextgram</CardTitle>
        <CardDescription>Login to continue</CardDescription>
      </CardHeader>
      <CardContent>
        <form>
          <div className="grid w-full items-center gap-4">
            <div className="flex flex-col space-y-1.5">
              <Label htmlFor="name">Name</Label>
              <Input id="name" placeholder="Name of your project" />
            </div>
           
          </div>
        </form>
      </CardContent>
      <CardFooter className="flex justify-between">
        <Button variant="outline">Cancel</Button>
        <Button>Deploy</Button>
      </CardFooter>
    </Card>



           
        <SignInButton>
    <Button className="">
        Sign in
    </Button>
    </SignInButton> 
       




            
            
                        
            
            
            </ConvexProviderWithClerk>

            </Unauthenticated>
        </ConvexProviderWithClerk>
    </ClerkProvider>
    )
}
export default ConvexClientProvider